<template>
  <div class="attention_of_teacher">
        <div class="attention_of_header">
            <div class="attention_of_header_left">
                 <van-icon name="arrow-left" @click="rollBackMymine"/>
            </div>
            <div class="attention_of_header_right">我的收藏</div>
        </div>
         <!-- 我的收藏列表部分 -->
         <div class="collect_list_container">
      <div class="collect_list_item" v-for="(item,index) in collectList" :key="index">
        <div class="list_left">
          <div class="item_top">
            <div>{{item.title}}</div>
            <div style="color:#333333;font-size:12px;">共{{item.section_num}}课时</div>
          </div>
          <div class="item_avatar">
            <img :src="item.teachersAvatar" width="20%"/>
            <span>{{item.teachers}}</span>
          </div>
          <div class="item_bottom">
            <div style="color:#595959;fnot-size:13px;">{{item.sales_num}}人已报名</div>
            <div :style="item.price==0?'color:green':'color:orange'">{{item.price|moneyFilter}}</div>
          </div>
        </div>
        <div class="list_right">
          <van-icon @click="cancelCollectClick" color="orange" name="star" />
        </div>
      </div>
    </div>
        <div class="attention_of_teacher_end">
            没有更多了
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            collectList: [],
            collectID: Number
        }
    },  
     methods: {
        rollBackMymine(){
            this.$router.go(-1)
        },
        cancelCollectClick() {
      this.$http
        .put(`/api/app/collect/cancel/${this.collectID}/1`)
        .then(resp => {
          console.log(resp.data);
          this.$http
            .get("/api/app/collect?page=1&limit=10&type=1&")
            .then(resp => {
              console.log(resp.data);
              resp.data.data.list.forEach(ele => {
                console.log(ele.collect_id);
                this.collectID = ele.collect_id;
              });
              this.collectList = resp.data.data.list;
            });
        });
    }
    },
    mounted(){
         this.$http.get("/api/app/collect?page=1&limit=10&type=1&").then(resp => {
      console.log(resp.data);
      resp.data.data.list.forEach(ele => {
        // console.log(ele.collect_id);
        this.collectID = ele.collect_id;
      });
      this.collectList = resp.data.data.list;
    });
    }
}
</script>
<style>
  .attention_of_teacher
{
    width: 100%;
    height: 667px;
    background: rgb(238, 237, 237);
}
   .attention_of_header
   {
       width: 100%;
       height: 3.5rem;
       background: white;
       display: inline-flex;
       justify-content: center;
       align-items: center;
       color: rgb(94, 93, 93);
       font-size: 1.2rem;
   } 
   .attention_of_header_left
   {
        width: 2.5rem;
        display: inline-flex;
       justify-content: center;
       align-items: center;
   }
   .attention_of_header_right
   {
         flex: 1;
        display: inline-flex;
       justify-content: center;
       align-items: center;
       margin-right: 2rem;
   }
   .attention_of_content
   {
       width: 93%;
       margin: 1rem auto 0;
   }
   .attention_of_content_info
   {
       width: 100%;
       height: 11rem;
       background: white;
       border-radius: 0.3rem;
       display: inline-flex;
       justify-content: center;
       align-items: center;
   }
   .attention_of_teacher_end
   {
       width: 100%;
       height: 4rem;
       display: inline-flex;
       justify-content: center;
       align-items: center;
       color: grey;
       font-size: 0.9rem;
   }
   .collect_list_container {
  width: 100%;
  background: #dbd7d7;
}
.collect_list_item {
  width: 100%;
  height: 180px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #fff;
}
.item_top {
  width: 100%;
  height: 45px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
}
.item_avatar {
  width: 100%;
  height: 40px;
  margin-left: 10px;
}
.item_bottom {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: Center;
}
.list_left {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.list_right {
  height: 100%;
  display: flex;
  align-content: flex-start;
  margin-top: 20px;
}
</style>